  z-index: 10;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  display: flex;
}

.card {
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  background: rgba(255, 255, 255, .533);
  border: 1px solid #777;
  border-radius: 10px;
