  border: 1px solid rgba(0, 0, 0, .133);
  border-radius: 8px;
  flex-grow: 1;
  flex-basis: 200px;
  margin: .5rem;
  padding: .8rem 1.2rem;
  box-shadow: 0 0 2px rgba(255, 255, 255, .467);
