html,
body {
  margin: 0;
  border: 0;
  color: #333;
  background-color: #293749;
  font-family: Helvetica, "Noto Sans", "Segoe UI", "Noto Sans CJK TC", "sourcesanspro", "微軟正黑體", Arial, sans-serif;
  font-weight: 300;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  margin-block-start: 0.5rem;
  margin-block-end: 0.5rem;
}

.container {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#tsparticles {
  position: fixed;
  width: 100%;
  height: 100%;
}

.card-container {
  width: 100%;
  max-width: 930px;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 40px;
}

.card {
  background: #fff8;
  backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid #777;
  padding: 1rem;
  margin: 40px 15px;
}

.hide {
  opacity: 0;
  transition: all 1s ease-out;
}

.cardTop {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 15px;
}

.avatar {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  background-image: url("assets/avatar.png");
  background-size: cover;
}

.basicInfo {
  text-align: left;
  margin: 0 2rem;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.skill {
  flex-grow: 1;
  flex-basis: 200px;
  margin: 0.5rem;
  border-radius: 8px;
  border: 1px solid #0002;
  box-shadow: 0px 0px 2px #fff7;
  padding: 0.8rem 1.2rem;
}

.skill ul {
  padding-inline-start: 1rem;
}

.skill-title {
  font-weight: 700;
  color: #273b55;
}

.skill li {
  list-style: circle;
  line-height: 2rem;
  margin: 0 0.5rem;
}

.buttons {
  display: flex;
  justify-content: end;
  margin: 14px 0;
}

.buttons > .btn {
  margin: 8px;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 400;
  color: #f7f7f7;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  padding: 10px 14px;
  transition: all 0.3s;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.1);
}

.buttons > .btn:hover {
  color: #f7f7f7;
  background-color: #5cded1;
  border: 1px solid #89f0e6;;
  box-shadow: 0px 0px 8px #24ead6;
}

.copyright {
  z-index: 20;
  padding: 20px;
  color: #777;
}
.copyright a {
  color: #f7f7f7;
  text-decoration: none;
}